import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input } from 'antd';
import {
  getPrioritizeObjectById,
  updatePrioritizeObject,
  createPrioritizeObject,
} from 'services/prioritizeObject';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormPrioritizeObjectPage' })
class FormPrioritizeObjectPage extends Component {
  state = {
    priorityObjectId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ priorityObjectId: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const res = await getPrioritizeObjectById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { priorityObjectId } = this.state;
    try {
      if (!priorityObjectId) {
        await createPrioritizeObject(values);
      } else {
        await updatePrioritizeObject({ id: priorityObjectId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { priorityObjectId } = this.state;
    return (
      <Card title={`${priorityObjectId ? 'Cập nhật' : 'Thêm'} đối tượng ưu tiên`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item {...formItemLayout} label="Tên" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormPrioritizeObjectPage {...props} />
  </Page>
));



